import { authHeader, handleResponse } from "../utils/utils";
import { API_BASE, COIN_TYPES } from "../config/constants";

const getWalletBalance = async (address, coinType=1) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader()}
    };

    const type = COIN_TYPES[coinType].toLowerCase();
    const response = fetch(`${API_BASE}/web3/balance/${type}/${address}`, requestOptions)
    return await handleResponse(response);
}

const trackTransactions = async (address, coinAmount=0, coinType=1, bootTime) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify({ time: bootTime, value: coinAmount })
    };

    const type = COIN_TYPES[coinType].toLowerCase();
    const response = fetch(`${API_BASE}/web3/track/${type}/${address}`, requestOptions)
    return await handleResponse(response);
}

export const web3Service = {
    getWalletBalance,
    trackTransactions
};
