import { useEffect } from 'react'
import { useDispatch } from 'react-redux';
import { Navbar } from './Navbar';

const Layout = ({
  children,
  showNavbar = true,
  navbarCustomSettingComponent,
  layoutClass = 'bg-app-form-border',
  searchBar,
  searchKeyword,
  showNormal = false,
  noRealestate = false,
  walletNav = true,
  faqBtn = false,
  classes = ''
}) => {
  const dispatch = useDispatch();

  useEffect(() => {
    // const init = async () => {
    //   const queryOpts = { name: 'clipboard-read', allowWithoutGesture: false };
    //   await navigator.permissions.query(queryOpts);
    //   // Will be 'granted', 'denied' or 'prompt':
    // }
    // init();
  }, [])
  return (
    <div className={`w-full min-h-screen bg-[#404040] ${layoutClass}`}>
      {showNavbar && (
        showNormal ? 
        <div className="px-6 md:px-10 lg:px-12 py-2">
          <Navbar normalBar={true} disableRealestate={noRealestate}/>
        </div> :
        <div className="px-6 md:px-10 lg:px-12 py-2">
          <Navbar
            customSettingComponent={navbarCustomSettingComponent}
            searchBar={searchBar}
            searchKeyword={searchKeyword}
            disableRealestate={noRealestate}
            walletNav={walletNav}
            faqBtn={faqBtn}
          />
        </div>
      )}
      <div className={`${classes}`}>
        {children}
      </div>
    </div>
  );
};

export default Layout;
