import {useState, useEffect, createRef, Fragment} from 'react';
import { selectUser } from '../../redux/userReducer';
import { selectCurOrders, selectCurCount, selectCoinType, addCurOrders, cleanCurOrders, selectVAT, setVAT, setCoinType, selectCurrentGroup, setCurrentGroup } from '../../redux/orderReducer';
import { useSelector, useDispatch } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash, faFloppyDisk, faMinus, faPlus, faCaretLeft, faUser, faPercent } from '@fortawesome/free-solid-svg-icons';
import { selectProducts, getProducts } from '../../redux/productReducer';
import { floatAdd, floatMul, getCoinAmount } from '../../utils/utils';
// import ReactTooltip from "react-tooltip";
import { SUPPORT_COINS } from "../../config/constants";
import { QRCodeSVG } from 'qrcode.react';
// import { Link } from 'react-router-dom';
import { openSnackBar } from '../../redux/snackBarReducer';
import { selectCustomer, updateOrderData, setCustomer } from '../../redux/orderReducer';
import { selectAllUsercustomers, getAllUsercustomerData } from '../../redux/userReducer';
import CartsModal from './merchant_group/modals/carts.modal';
import { COIN_EXPLORER_LINK } from '../../config/constants';
import {
    selectCoinAllowBTC,
    selectCoinAllowETH,
    selectCoinAllowBNB,
    selectCoinAllowXRP,
    selectCoinAllowUSDT,
    selectCoinAllowCPOS,
    selectAllowDiscount
} from '../../redux/settingReducer';
import WaitingPaymentModal from './merchant_group/modals/waiting.payment.modal';
import { selectCoinData } from '../../redux/web3Reducer';
import { Menu, Transition } from "@headlessui/react";

const Sidebar = () => {
  const dispatch = useDispatch();
  const user = useSelector(selectUser);
  const orders = useSelector(selectCurOrders);
  const orderCount = useSelector(selectCurCount);
  const products = useSelector(selectProducts);
  const vat = useSelector(selectVAT);
  const coinType = useSelector(selectCoinType);
  const customer = useSelector(selectCustomer);
  const customers = useSelector(selectAllUsercustomers);
  const [subtotalCost, setSubTotalCost] = useState(0);
  const [cartsModal, setCartsModal] = useState(false);
  const [paymentModal, setPaymentModal] = useState(false);
  const coinBTC = useSelector(selectCoinAllowBTC);
  const coinETH = useSelector(selectCoinAllowETH);
  const coinBNB = useSelector(selectCoinAllowBNB);
  const coinXRP = useSelector(selectCoinAllowXRP);
  const coinUSDT = useSelector(selectCoinAllowUSDT);
  const coinCPOS = useSelector(selectCoinAllowCPOS);
  const [cposOrdered, setCposOrdered] = useState({});
  const [coinGecko, setCoinGecko] = useState(false);
  const coinData = useSelector(selectCoinData);
  const discount = useSelector(selectAllowDiscount);

  useEffect(() => {
    dispatch(getProducts());
  }, []);

  useEffect(() => {
    let cost = 0;
    orders.map((o, index) => {
        const p = getProduct(o.productId);
        cost = floatAdd(cost, floatMul(p.price, o.amount));
    });
    setSubTotalCost(cost);
    
  }, [orderCount])

  const getProduct = (id) => {
    const p = products.filter((e) => e._id === id);
    return p[0];
  }

  const handleMinus = (p) => {
    const order = {
        productId: p._id,
        amount: -1
    }
    dispatch(addCurOrders(order));
  }

  const handlePlus = (p) => {
    const order = {
        productId: p._id,
        amount: 1
    }
    dispatch(addCurOrders(order));
  }

  const handleClear = () => {
    scrollToGroup(0);
    dispatch(cleanCurOrders());
  }

  const handleSave = async (name) => {
    const coinAmount = await getCoinAmount(coinType, subtotalCost, vat, coinData, discount);
    const order = {
        name: name,
        merchantId: user._id,
        customerId: customer ? customer._id:'000000000000000000000000',
        order: orders,
        coinType: coinType,
        coinAmount: coinAmount
    };
    dispatch(updateOrderData(0, order));
    scrollToGroup(0);
    dispatch(cleanCurOrders());
  }

  const handleCoinType = (type) => {
    dispatch(setCoinType(type));
  }

  const getCoinValidate = (index) => {
      let valid = false;
      switch (index) {
          case 0:
              valid = coinBTC;
              break;
          case 1:
              valid = coinETH;
              break;
          case 2:
              valid = coinBNB;
              break;
          case 3:
              valid = coinXRP;
              break;
          case 4:
              valid = coinUSDT;
              break;
          case 5:
              valid = coinCPOS;
              break;
      }

      return valid;
  }

  useEffect(() => {
    scrollToGroup(currentGroup, false);
  }, [])

  useEffect( () => {
    dispatch(getAllUsercustomerData());
  }, [])

  const form_groups = [
    <div key={1} className='w-full h-full overflow-x-auto text-[#404040]'>
        {
            orders.map((o, index) => {
                const p = getProduct(o.productId);
                return (
                    <div key={index} className="border-b border-gray-900 py-4">    
                        <div className='flex justify-between text-md'>
                            <div>{p.name}</div>
                            <div>{p.price.toFixed(2)}</div>
                        </div>
                        <div className='flex justify-between text-sm'>
                            <div>SKU: {p.sku}</div>
                            <div className='flex justify-center items-center'>
                                <FontAwesomeIcon 
                                    icon={faMinus}
                                    className="cursor-pointer"
                                    onClick={()=>handleMinus(p)}
                                />
                                <p className='px-2 select-none'>{o.amount}</p>
                                <FontAwesomeIcon 
                                    icon={faPlus} 
                                    className="cursor-pointer"
                                    onClick={()=>handlePlus(p)}
                                />
                            </div>
                        </div>
                    </div>
                )
            })
        }
    </div>,
    <div key={2} className='w-full h-full grid grid-cols-2 gap-2 text-center py-[2rem] px-[2rem]'>
        {
            SUPPORT_COINS.map((item, index) => {
                const valid = getCoinValidate(index)
                return (
                valid ? <div key={index} onClick={() => handleCoinType(index)} className={`w-[8rem] h-[8rem] flex justify-center items-center ${coinType === index ? 'bg-[#B9B9B9]':''} border border-[#404040] text-[#404040] rounded-[1rem] select-none cursor-pointer`}>
                    <p className='w-[60%]'>{item}</p>
                </div> : null
                )
            })
        }
    </div>,
    <div key={3} className='w-full h-full'>
        <div className='w-full h-[5rem]'>            
        </div>
        <div className='w-full flex justify-center'>            
            <QRCodeSVG 
                value={user ? user.walpublic : '0x0000000000'}
                level="H"
                size={256}
            />
        </div>
        <div className='w-full text-center'>
            <p className='text-red-600 text-md my-[1rem]'>Please ensure block is confirmed before parting with goods/rendering services.</p>
            {user && <p className='text-[#404040] font-bold text-md my-[1rem]'>Check status of transaction here: <a href={`${COIN_EXPLORER_LINK[coinType]}${user.walpublic}`}>{`${COIN_EXPLORER_LINK[coinType]}${user.walpublic}`}</a></p>}
        </div>
    </div>
  ]

  const currentGroup = useSelector(selectCurrentGroup);

  const refs = form_groups.reduce((acc, val, i) => {
    acc[i] = createRef();
    return acc;
  }, {});

  const totalGroups = form_groups.length;
  const scrollToGroup = (i, mode=true) => {
    dispatch(setCurrentGroup(i));
    refs[i].current.scrollIntoView({
      behavior: mode ? 'smooth':'instant',
      block: 'nearest',
      inline: 'start',
    });
  };
  const nextGroup = () => {
    if (currentGroup >= totalGroups - 1) {
      scrollToGroup(0);
      return;
    } else {
      scrollToGroup(currentGroup + 1);
    }
  };
  
  const previousGroup = () => {
    if (currentGroup === 0) {
      scrollToGroup(totalGroups - 1);
      return;
    } else {
      scrollToGroup(currentGroup - 1);
    }
  };

  const handleNextGroup = async () => {
    if (currentGroup === 0) {
        if (orders.length === 0) {
            dispatch(openSnackBar({ message: "Empty orders.", status: 'error' }));
            return;
        }
        // if (!customer) {
        //     dispatch(openSnackBar({ message: "Select customer.", status: 'error' }));
        //     return;
        // }
    }
    if (currentGroup === 2) {
        /// waiting receive payment
        const coinAmount = await getCoinAmount(coinType, subtotalCost, vat, coinData, discount);
        const order = {
            name: "cpos_ordered",
            merchantId: user._id,
            customerId: customer ? customer._id: '000000000000000000000000',
            order: orders,
            coinType: coinType,
            coinAmount: coinAmount,
            waiting: true
        };
        setCposOrdered(order);
        setPaymentModal(true);
        dispatch(updateOrderData(0, order));
        dispatch(cleanCurOrders());
    }

    nextGroup();
  }

  const handlePrevGroup = () => {
    previousGroup();
  }

  const getHandleTitle = () => {
    let ret = "";
    switch (currentGroup) {
        case 0:
            ret = "Checkout"
            break;
        case 1:
            ret = "Generate QR"
            break;
        case 2:
            ret = "Complete Order"
            break;
    }
    return ret;
  }

  const getSidebarTitle = () => {
    let ret = "";
    switch (currentGroup) {
        case 0:
            ret = customer ? customer.name : '';
            break;
        case 1:
            ret = "Select crypto:"
            break;
        case 2:
            ret = "Customer to scan QR to complete order:"
            break;
    }
    return ret;
  }

  return (
    <div className='bg-[#D9D9D9] h-[calc(100vh-80px)] w-[0px] sm:w-[360px] md:w-[360px] lg:w-[360px] xl:w-[360px] transition-all'>
        <div className='w-full h-full relative'>
            <div className='w-full h-[2rem] flex items-center text-[#404040]'>
                {currentGroup!==0 && <button 
                    className={`w-[2rem] h-[2rem] text-md rounded-[6px] leading-[2rem] text-[#404040]`}
                    onClick={() => handlePrevGroup()}
                ><FontAwesomeIcon icon={faCaretLeft}/></button>}
                {/* {currentGroup===0 && <button 
                    className={`w-[2rem] h-[2rem] text-md rounded-[6px] leading-[2rem] text-[#404040]`}
                ><FontAwesomeIcon icon={faUser}/></button>} */}
                <Menu as="div" className="relative inline-block text-left nav-menu mt-1">
                    <Menu.Button className={`w-[2rem] h-[2rem] text-md rounded-[6px] leading-[2rem] text-[#404040]`}>
                        <FontAwesomeIcon icon={faUser}/>
                    </Menu.Button>

                    {(customers && customers.length > 0) && <Transition
                        as={Fragment}
                        enter="transition ease-out duration-100"
                        enterFrom="transform opacity-0 scale-95"
                        enterTo="transform opacity-100 scale-100"
                        leave="transition ease-in duration-75"
                        leaveFrom="transform opacity-100 scale-100"
                        leaveTo="transform opacity-0 scale-95"
                        >
                        <Menu.Items className="absolute right-0 mt-2 w-56 origin-top-right divide-y divide-gray-100 rounded-sm bg-white shadow-lg ring-2 ring-gray-200 focus:outline-none z-10">
                            <div className="px-1 py-1">  
                                {
                                    customers.map((c, index) => {
                                        return (
                                            <Menu.Item key={index}>
                                                {({ active }) => (
                                                <button
                                                    className={`${
                                                    active ? "bg-[#404040] text-white" : "text-gray-900"
                                                    } group  w-full items-center rounded-sm px-2 py-2 text-sm text-center`}
                                                    onClick={()=>dispatch(setCustomer(c))}
                                                >
                                                    {c.name}
                                                    &nbsp;<i className="fa fa-right-from-backet">&nbsp;</i>
                                                </button>
                                                )}
                                            </Menu.Item>
                                        );
                                    })
                                }   
                            </div>
                        </Menu.Items>
                    </Transition>}
                </Menu>
                <p>{getSidebarTitle()}</p>
            </div>
            <div className='w-full h-[68vh] px-4'>
                <div className='relative w-full h-full'>
                    <div className="carousel w-full h-full">
                        {form_groups.map((form_group, i) => {
                            return (
                            <div className='w-full h-full flex-shrink-0' key={i} ref={refs[i]}>  
                                {form_group} 
                            </div>
                            )
                        })}
                    </div>
                </div>
            </div>
            <div className='w-full absolute bottom-0 px-6 py-2 text-[#404040]'>
                <div className='flex justify-between'>
                    <p>Subtotal</p>
                    <p>{subtotalCost.toFixed(2)}</p>                    
                </div>
                <div className='flex justify-between'>
                    <p>Discount {`${vat > 0 ? '(' + floatMul(vat, 100) + '%)' : ''}`}</p>
                    <p>{floatMul(subtotalCost, vat).toFixed(2)}</p>
                </div>
                <div className='flex justify-between font-bold'>
                    <p>Total</p>
                    <p>{floatAdd(subtotalCost, floatMul(subtotalCost, -vat)).toFixed(2)}</p>
                </div>
                <div className='flex justify-between mt-2'>
                    <div className='flex justify-between gap-2'>                        
                        <button 
                            className={`w-[2rem] h-[2rem] text-md rounded-[6px] leading-[2rem] bg-[#404040] text-gray-100`}
                            onClick={() => handleClear()}
                        ><FontAwesomeIcon icon={faTrash}/></button>
                        <button 
                            className={`w-[2rem] h-[2rem] text-md rounded-[6px] leading-[2rem] bg-[#404040] text-gray-100`}            
                            onClick={() => {                                
                                if (orders.length === 0) {
                                    dispatch(openSnackBar({ message: "Empty orders.", status: 'error' }));
                                    return;
                                }
                                // if (!customer) {
                                //     dispatch(openSnackBar({ message: "Select customer.", status: 'error' }));
                                //     return;
                                // }
                                setCartsModal(true);
                            }}
                        ><FontAwesomeIcon icon={faFloppyDisk}/></button>
                        { currentGroup === 0 && <Menu as="div" className="relative inline-block text-left nav-menu">
                            <Menu.Button className={`w-[2rem] h-[2rem] text-md rounded-[6px] leading-[2rem] bg-[#404040] text-gray-100`}>
                                <FontAwesomeIcon icon={faPercent}/>
                            </Menu.Button>

                            <Transition
                                as={Fragment}
                                enter="transition ease-out duration-100"
                                enterFrom="transform opacity-0 scale-95"
                                enterTo="transform opacity-100 scale-100"
                                leave="transition ease-in duration-75"
                                leaveFrom="transform opacity-100 scale-100"
                                leaveTo="transform opacity-0 scale-95"
                                >
                                <Menu.Items className="-top-4 transform -translate-y-full absolute right-0 mt-2 w-[8rem] origin-top-right divide-y divide-gray-100 rounded-md bg-[#404040] focus:outline-none z-10">
                                    <div className="px-2 py-2 grid grid-cols-3 gap-2">  
                                        <Menu.Item>
                                            <button
                                                className={`${
                                                vat === 0.05 ? "bg-[#6A6A6A] text-white" : "bg-[#D9D9D9] text-gray-900"
                                                } w-[2rem] h-[2rem] text-md text-center rounded-[6px] leading-[2rem] text-gray-100 px-auto`}
                                                onClick={() => dispatch(setVAT(0.05))}
                                            >
                                                <p>5%</p>
                                                &nbsp;<i className="fa fa-right-from-backet">&nbsp;</i>
                                            </button>
                                        </Menu.Item>
                                        <Menu.Item>
                                            <button
                                                className={`${
                                                vat === 0.1 ? "bg-[#6A6A6A] text-white" : "bg-[#D9D9D9] text-gray-900"
                                                } w-[2rem] h-[2rem] text-md text-center rounded-[6px] leading-[2rem] text-gray-100 px-auto`}
                                                onClick={() => dispatch(setVAT(0.1))}
                                            >
                                                <p>10%</p>
                                                &nbsp;<i className="fa fa-right-from-backet">&nbsp;</i>
                                            </button>
                                        </Menu.Item>
                                        <Menu.Item>
                                            <button
                                                className={`${
                                                vat === 0.15 ? "bg-[#6A6A6A] text-white" : "bg-[#D9D9D9] text-gray-900"
                                                } w-[2rem] h-[2rem] text-md text-center rounded-[6px] leading-[2rem] text-gray-100 px-auto`}
                                                onClick={() => dispatch(setVAT(0.15))}
                                            >
                                                <p>15%</p>
                                                &nbsp;<i className="fa fa-right-from-backet">&nbsp;</i>
                                            </button>
                                        </Menu.Item>
                                        <Menu.Item>
                                            <button
                                                className={`${
                                                vat === 0.2 ? "bg-[#6A6A6A] text-white" : "bg-[#D9D9D9] text-gray-900"
                                                } w-[2rem] h-[2rem] text-md text-center rounded-[6px] leading-[2rem] text-gray-100 px-auto`}
                                                onClick={() => dispatch(setVAT(0.2))}
                                            >
                                                <p>20%</p>
                                                &nbsp;<i className="fa fa-right-from-backet">&nbsp;</i>
                                            </button>
                                        </Menu.Item>
                                        <Menu.Item>
                                            <button
                                                className={`${
                                                vat === 0.25 ? "bg-[#6A6A6A] text-white" : "bg-[#D9D9D9] text-gray-900"
                                                } w-[2rem] h-[2rem] text-md text-center rounded-[6px] leading-[2rem] text-gray-100 px-auto`}
                                                onClick={() => dispatch(setVAT(0.25))}
                                            >
                                                <p>25%</p>
                                                &nbsp;<i className="fa fa-right-from-backet">&nbsp;</i>
                                            </button>
                                        </Menu.Item>
                                        <Menu.Item>
                                            <button
                                                className={`${
                                                vat === 0.0 ? "bg-[#6A6A6A] text-white" : "bg-[#D9D9D9] text-gray-900"
                                                } w-[2rem] h-[2rem] text-[0.6rem] text-center rounded-[6px] leading-[2rem] text-gray-100 px-auto`}
                                                onClick={() => dispatch(setVAT(0.0))}
                                            >
                                                <p>Clean</p>
                                                &nbsp;<i className="fa fa-right-from-backet">&nbsp;</i>
                                            </button>
                                        </Menu.Item>
                                    </div>
                                </Menu.Items>
                            </Transition>
                        </Menu>}                        
                    </div>
                    <button 
                        className={`w-[9rem] h-[2rem] text-md rounded-[6px] leading-[2rem] bg-[#404040] text-gray-100`}  
                        onClick={() => handleNextGroup()}          
                    >{coinGecko ? 'Calculating' : getHandleTitle()}</button>
                </div>
            </div>
        </div>
        <CartsModal isModal={cartsModal} setIsModal={setCartsModal} registerFlag={true} handleRegister={handleSave}/>
        <WaitingPaymentModal isModal={paymentModal} setIsModal={setPaymentModal} cposOrdered={cposOrdered}/>
    </div>
  );
}

export default Sidebar;